.tab-button {
    background-color: transparent;
    margin-top: 10px;
    margin-bottom: 10px;
    border-style: none;
    color: #ffffff;
    font-size: 20px;
    outline: none;
    min-width: 60px;
    height: 40px;
    margin-right: 20px;
    padding: 10px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }

  .highlight-text {
    color: #fec653;
    border-bottom: solid;
    border-bottom-color: #fec653;
  }